import { Component, OnInit } from '@angular/core'
import { fuseAnimations } from '@fuse/animations'
import { FormGroup, FormBuilder, FormControl } from '@angular/forms'
import { Router } from '@angular/router'
import * as crypto from 'crypto-js'

import { UtilityService } from 'app/services/utility.service'
import { MyProfileService } from 'app/services/my-profile.service'

import { MomentDateAdapter } from '@angular/material-moment-adapter';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';

import * as _moment from 'moment';

const moment = _moment;

export const MY_FORMATS = {
    parse: {
        dateInput: 'LL',
    },
    display: {
        dateInput: 'LL',
        monthYearLabel: 'MMM YYYY',
        dateA11yLabel: 'LL',
        monthYearA11yLabel: 'MMMM YYYY',
    },
};

@Component({
    selector: 'edit-my-profile',
    templateUrl: './edit-my-profile.component.html',
    animations: fuseAnimations,
    providers: [
        UtilityService,
        MyProfileService,

        { provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE] },
        { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS }
    ]
})

export class EditMyProfileComponent implements OnInit {
    isLoading: boolean
    isSuccessFetch: boolean
    isSubmitData: boolean

    failMessage: string
    image: string
    user_imei: string
    phone_type: string
    phone_vendor: string
    phone_os: string
    os_version: string
    app_version: string

    arrDataReligion = []
    arrDataProfile = []

    formProfile: FormGroup

    nim = new FormControl('')
    nama = new FormControl('')
    tgl_lahir = new FormControl(moment())
    tempat_lahir = new FormControl('')
    jenis_kelamin = new FormControl('')
    religion_id = new FormControl('')
    no_ktp = new FormControl('')
    no_sim = new FormControl('')
    no_npwp = new FormControl('')
    tinggi_badan = new FormControl('')
    berat_badan = new FormControl('')
    golongan_darah = new FormControl('')
    alamat = new FormControl('')
    provinsi = new FormControl('')
    kota_kabupaten = new FormControl('')
    kecamatan = new FormControl('')
    kelurahan = new FormControl('')
    kode_pos = new FormControl('')
    email = new FormControl('')
    no_hp1 = new FormControl('')
    no_hp2 = new FormControl('')

    constructor(
        private router: Router,
        private formBuilder: FormBuilder,

        private utilityService: UtilityService,
        private myProfileService: MyProfileService
    ) {
        this.isSubmitData = false

        this.formProfile = this.formBuilder.group({
            nama: this.nama,
            tgl_lahir: this.tgl_lahir,
            tempat_lahir: this.tempat_lahir,
            jenis_kelamin: this.jenis_kelamin,
            religion_id: this.religion_id,
            email: this.email,
            no_hp1: this.no_hp1
        })
    }

    ngOnInit(): void {
        this.loadReligion()
    }

    timeoutCondition(hitApi: any, timeout: any) {
        hitApi.unsubscribe()
        clearTimeout(timeout)

        this.isLoading = false
        this.isSuccessFetch = false

        this.failMessage = "Timeout, Mohon periksa koneksi internet anda"
    }

    loadReligion() {
        this.isLoading = true

        let ParamReq = {
            user_id: this.utilityService.user_id,
            // school_id: this.utilityService.school_id
        }

        let getDataProfile = this.myProfileService.loadReligion(ParamReq)
            .subscribe((result: any) => {
                clearTimeout(timeout)

                if (result.status == 200) {
                    this.arrDataReligion = result.list_data

                    this.loadProfile()
                } else {
                    this.isLoading = false
                    this.isSuccessFetch = false

                    this.failMessage = result.message
                }
            }, _error => {
                clearTimeout(timeout)

                this.isLoading = false
                this.isSuccessFetch = false

                if (_error.error.code == 0) {
                    this.failMessage = "Maaf, tidak dapat menghubungkan ke server"
                } else if (_error.status == 401) {
                    this.router.navigate(['/login'])
                } else {
                    this.failMessage = "Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi"
                }
            })

        let timeout = setTimeout(() => {
            this.timeoutCondition(getDataProfile, timeout)
        }, 10000)
    }

    loadProfile() {
        let ParamReq = {
            user_id: this.utilityService.user_id,
            // school_id: this.utilityService.school_id,
            // user_type: this.utilityService.user_type
        }

        let getDataProfile = this.myProfileService.getDataProfile(ParamReq)
            .subscribe((result: any) => {
                clearTimeout(timeout)

                this.isLoading = false

                if (result.status == 200) {
                    this.isSuccessFetch = true

                    this.nim.setValue(result.data.nim)
                    this.nama.setValue(result.data.nama)
                    this.tgl_lahir.setValue(new Date(result.data.tgl_lahir))
                    this.tempat_lahir.setValue(result.data.tempat_lahir)
                    this.jenis_kelamin.setValue(result.data.jenis_kelamin)
                    this.religion_id.setValue(result.data.religion_id)
                    this.no_ktp.setValue(result.data.no_ktp)
                    this.no_sim.setValue(result.data.no_sim)
                    this.no_npwp.setValue(result.data.no_npwp)
                    this.tinggi_badan.setValue(result.data.tinggi_badan.slice(0, -2))
                    this.berat_badan.setValue(result.data.berat_badan.slice(0, -2))
                    this.golongan_darah.setValue(result.data.golongan_darah)
                    this.alamat.setValue(result.data.alamat)
                    this.provinsi.setValue(result.data.provinsi)
                    this.kota_kabupaten.setValue(result.data.kota_kabupaten)
                    this.kecamatan.setValue(result.data.kecamatan)
                    this.kelurahan.setValue(result.data.kelurahan)
                    this.kode_pos.setValue(result.data.kode_pos)
                    this.email.setValue(result.data.email)
                    this.no_hp1.setValue(result.data.no_hp1)
                    this.no_hp2.setValue(result.data.no_hp2)
                    this.image = result.data.image
                } else {
                    this.isSuccessFetch = false

                    this.failMessage = result.message
                }
            }, _error => {
                clearTimeout(timeout)

                this.isLoading = false
                this.isSuccessFetch = false

                if (_error.error.code == 0) {
                    this.failMessage = "Maaf, tidak dapat menghubungkan ke server"
                } else if (_error.status == 401) {
                    this.router.navigate(['/login'])
                } else {
                    this.failMessage = "Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi"
                }
            })

        let timeout = setTimeout(() => {
            this.timeoutCondition(getDataProfile, timeout)
        }, 10000)
    }

    editProfile() {
        this.isSubmitData = true

        let ParamReq = {
            user_id: this.utilityService.user_id,
            // school_id: this.utilityService.school_id,
            // user_type: this.utilityService.user_type,
            nim: this.nim.value,
            nid: "",
            nidn: "",
            nidk: "",
            nama: this.nama.value,
            jenis_kelamin: this.jenis_kelamin.value,
            tgl_lahir: (new Date(this.tgl_lahir.value)).getTime(),
            tempat_lahir: this.tempat_lahir.value,
            no_ktp: this.no_ktp.value,
            no_sim: this.no_sim.value,
            no_npwp: this.no_npwp.value,
            no_hp1: this.no_hp1.value,
            no_hp2: this.no_hp2.value,
            tinggi_badan: this.tinggi_badan.value + "cm",
            berat_badan: this.berat_badan.value + "kg",
            alamat: this.alamat.value,
            provinsi: this.provinsi.value,
            kota_kabupaten: this.kota_kabupaten.value,
            kecamatan: this.kecamatan.value,
            kelurahan: this.kelurahan.value,
            kode_pos: this.kode_pos.value,
            golongan_darah: this.golongan_darah.value,
            email: this.email.value,
            religion_id: this.religion_id.value,
            image: this.image,
            user_imei: this.user_imei,
            phone_type: this.phone_type,
            phone_vendor: this.phone_vendor,
            phone_os: this.phone_os,
            os_version: this.os_version,
            app_version: this.app_version
        }

        let editProfile = this.myProfileService.editProfile(ParamReq)
            .subscribe((result: any) => {
                clearTimeout(timeout)

                if (result.status == 200) {

                    // this.isSubmitData = false

                    // if (localStorage.getItem('cva')) {
                    //     var encryptProfile = crypto.AES.encrypt(JSON.stringify({
                    //         user_id: this.utilityService.user_id,
                    //         user_type: this.utilityService.user_type,
                    //         fullname: this.nama.value,
                    //         foto: this.image,
                    //         token: this.utilityService.token
                    //     }), 'ArigoBudiTiar2019').toString()
                    //     localStorage.removeItem('cva')
                    //     localStorage.setItem('cva', encryptProfile)
                    // } else if (sessionStorage.getItem('cva')) {
                    //     var encryptProfile = crypto.AES.encrypt(JSON.stringify({
                    //         user_id: this.utilityService.user_id,
                    //         user_type: this.utilityService.user_type,
                    //         fullname: this.nama.value,
                    //         foto: this.image,
                    //         token: this.utilityService.token
                    //     }), 'ArigoBudiTiar2019').toString()
                    //     sessionStorage.removeItem('cva')
                    //     sessionStorage.setItem('cva', encryptProfile)
                    // }

                    this.utilityService.dialogMessage("Pesan", "Profil berhasil diperbarui", "Kembali")

                    this.router.navigate(["/profile"])
                } else {
                    this.isSubmitData = false

                    this.utilityService.dialogMessage("Pesan", result.message, "Oke")
                }
            }, _error => {
                clearTimeout(timeout)

                if (_error.status == 0) {
                    this.isSubmitData = false

                    this.utilityService.dialogMessage("Pesan", "Maaf, tidak dapat menghubungkan ke server", "Kembali")
                } else if (_error.status == 401) {
                    this.router.navigate(['/login'])
                } else {
                    this.isSubmitData = false

                    this.utilityService.dialogMessage("Pesan", "Maaf, sepertinya telah terjadi kesalahan server, mohon coba beberapa saat lagi", "Kembali")
                }
            })

        let timeout = setTimeout(() => {
            editProfile.unsubscribe()
            clearTimeout(timeout)

            this.isSubmitData = false

            this.utilityService.dialogMessage("Pesan", "Timeout, Mohon periksa koneksi internet anda", "Kembali")
        }, 10000)
    }
}
