import { NgModule } from '@angular/core'
import { RouterModule } from '@angular/router'
import { TranslateModule } from '@ngx-translate/core'
import {
    MatButtonModule,
    MatDatepickerModule,
    MatDividerModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatOptionModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatSelectModule,
    MatSnackBarModule,
    MatTabsModule,
    MatToolbarModule,
    MatTooltipModule
} from '@angular/material'

import { FuseSharedModule } from '@fuse/shared.module'

import { AuthGuard } from 'app/guard/guard-auth.guard'

import { NumberOnlyModule } from 'app/directive/numberOnly.module'

import { AvatarModule } from 'ngx-avatar'

import { ImageUploadModule } from 'ng2-imageupload'
import { Ng2ImgMaxModule } from 'ng2-img-max'

import { AngularFireModule } from 'angularfire2'
import { AngularFireStorageModule } from 'angularfire2/storage'

import { MyProfileComponent } from './my-profile/my-profile.component'

import { EditMyProfileComponent } from './edit-my-profile/edit-my-profile.component'

const routes = [
    {
        path: '',
        component: MyProfileComponent,
        canActivate: [AuthGuard]
    },
    {
        path: 'edit',
        component: EditMyProfileComponent,
        canActivate: [AuthGuard]
    }
]

@NgModule({
    declarations: [
        MyProfileComponent,

        EditMyProfileComponent,
    ],
    imports: [
        RouterModule.forChild(routes),

        TranslateModule,

        FuseSharedModule,

        NumberOnlyModule,

        AvatarModule,

        ImageUploadModule,
        Ng2ImgMaxModule,

        AngularFireModule.initializeApp({
            apiKey: "AIzaSyAkvYWxjSZW8Av8O-0NhoknImj2mGEJ5CY",
            authDomain: "edutren-firebase.firebaseapp.com",
            databaseURL: "https://edutren-firebase.firebaseio.com",
            projectId: "edutren-firebase",
            storageBucket: "edutren-firebase.appspot.com",
            messagingSenderId: "34515332823"
        }),
        AngularFireStorageModule,

        MatButtonModule,
        MatDatepickerModule,
        MatDividerModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatMenuModule,
        MatOptionModule,
        MatProgressBarModule,
        MatProgressSpinnerModule,
        MatSelectModule,
        MatSnackBarModule,
        MatTabsModule,
        MatToolbarModule,
        MatTooltipModule
    ]
})

export class ProfileModule { }