import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { UtilityService } from './utility.service';

@Injectable()
export class MyProfileService {
    constructor(
        private _http: HttpClient,
        private utilityService: UtilityService
    ) { }

    getDataProfile(ParamReq: any) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.utilityService.token
            })
        }

        return this._http
            .post(this.utilityService.server + "/api/user/get-data-edit-profile", ParamReq, httpOptions)
    }

    loadReligion(ParamReq: any) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.utilityService.token
            })
        }

        return this._http
            .post(this.utilityService.server + "/api/universal-lookup/list-religion", ParamReq, httpOptions)
    }

    editProfile(ParamReq: any) {
        const httpOptions = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + this.utilityService.token
            })
        }

        return this._http
            .post(this.utilityService.server + "/api/user/edit-profile", ParamReq, httpOptions)
    }
}